	export default{
		data() {
			return{
                imageURL: '../../static/img-percenter/bg.jpg',
				name:'',
				headimg:''
			}
		},
		onLoad(){
			let _this = this
			uni.getStorage({
				key:"name",
				success(e){
					_this.name = e.data
					uni.request({
						url:'http://ceshi3.dishait.cn/api/login',
						data:{
							username : 'user2',
							password : 'zcmcss'
						},
						method:'POST',
						success:(res) =>{
							_this.headimg = res.data.data.avatar
						}
					})
				},
				fail: err => {
				    _this.name = "未登录"
					_this.headimg = '../../static/img-percenter/log.jpg'
				}
			})
		},
		methods:{
			/**
			 * @modifiedData:跳转到修改资料页面
			 */
			modifiedData(){
				let _this = this
				uni.getStorage({
					key:"name",
					success(e){
						_this.name = e.data
						uni.navigateTo({
							url:"../modified-data/modified-data?img="+_this.headimg+""
						})
					},
					fail: err => {
					    _this.name = "未登录"
						console.log(_this.name)
						uni.navigateTo({
							url:"../login/login"
						})
					}
				})
			},
			/**
			* @modifiedData:跳转到更多设置页面
			*/
			setClick(){
				uni.navigateTo({
					url:"../setting/setting"
				})
			}
		}
	}
